"use client";
import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card/Card";
import { Badge } from "@/components/ui/badge/Badge";
import {
  ChevronUp,
  ChevronDown,
  User,
  LogIn,
  Quote,
  TrendingUp,
  Users,
  Award,
} from "lucide-react";
import LoginForm from "@/components/LoginForm";
import { useToast } from "@/hooks/use-toast";

const Index = () => {
  const [showLogin, setShowLogin] = useState(false);
  const [votes, setVotes] = useState({
    quote1: { up: 45, down: 12 },
    quote2: { up: 32, down: 8 },
    quote3: { up: 67, down: 15 },
  });
  const { toast } = useToast();

  const handleVote = (quoteId: string, type: "up" | "down") => {
    if (!showLogin) {
      setVotes((prev) => ({
        ...prev,
        [quoteId]: {
          ...prev[quoteId],
          [type]: prev[quoteId][type] + 1,
        },
      }));
      toast({
        title: "Vote recorded!",
        description: `You voted ${type} on this quote.`,
      });
    } else {
      toast({
        title: "Please log in",
        description: "You need to be logged in to vote on quotes.",
        variant: "destructive",
      });
    }
  };

  const quotes = [
    {
      id: "quote1",
      text: "Democracy is not a spectator sport, it requires the active participation of all citizens.",
      author: "Barack Obama",
      category: "Democracy",
      votes: votes.quote1,
    },
    {
      id: "quote2",
      text: "The ballot is stronger than the bullet.",
      author: "Abraham Lincoln",
      category: "Voting",
      votes: votes.quote2,
    },
    {
      id: "quote3",
      text: "Your vote is your voice. Use it.",
      author: "Unknown",
      category: "Civic Duty",
      votes: votes.quote3,
    },
  ];

  if (showLogin) {
    return <LoginForm onBack={() => setShowLogin(false)} />;
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-indigo-100">
      {/* Header */}
      <header className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center space-x-3">
              <div className="w-10 h-10 bg-blue-600 rounded-lg flex items-center justify-center">
                <Quote className="h-6 w-6 text-white" />
              </div>
              <h1 className="text-2xl font-bold text-gray-900">VoteQuotes</h1>
            </div>
            <div className="flex items-center space-x-4">
              <Button variant="outline" size="sm">
                <Users className="h-4 w-4 mr-2" />
                Community
              </Button>
              <Button
                onClick={() => setShowLogin(true)}
                className="bg-blue-600 hover:bg-blue-700"
              >
                <LogIn className="h-4 w-4 mr-2" />
                Login
              </Button>
            </div>
          </div>
        </div>
      </header>

      {/* Hero Section */}
      <section className="py-20 px-4 sm:px-6 lg:px-8">
        <div className="max-w-4xl mx-auto text-center">
          <h2 className="text-5xl font-bold text-gray-900 mb-6 animate-fade-in">
            Vote on the Quotes That
            <span className="text-blue-600"> Shape Democracy</span>
          </h2>
          <p className="text-xl text-gray-600 mb-8 animate-fade-in">
            Discover powerful quotes about voting, democracy, and civic
            engagement. Vote for your favorites and see what resonates with the
            community.
          </p>
          <div className="flex justify-center space-x-4 animate-fade-in">
            <Button size="lg" className="bg-blue-600 hover:bg-blue-700">
              <TrendingUp className="h-5 w-5 mr-2" />
              Explore Quotes
            </Button>
            <Button variant="outline" size="lg">
              <Award className="h-5 w-5 mr-2" />
              Top Rated
            </Button>
          </div>
        </div>
      </section>

      {/* Stats Section */}
      <section className="py-12 bg-white">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8 text-center">
            <div className="animate-fade-in">
              <div className="text-4xl font-bold text-blue-600 mb-2">1,247</div>
              <div className="text-gray-600">Quotes Voted On</div>
            </div>
            <div className="animate-fade-in">
              <div className="text-4xl font-bold text-blue-600 mb-2">8,932</div>
              <div className="text-gray-600">Community Votes</div>
            </div>
            <div className="animate-fade-in">
              <div className="text-4xl font-bold text-blue-600 mb-2">456</div>
              <div className="text-gray-600">Active Voters</div>
            </div>
          </div>
        </div>
      </section>

      {/* Quotes Section */}
      <section className="py-16 px-4 sm:px-6 lg:px-8">
        <div className="max-w-4xl mx-auto">
          <h3 className="text-3xl font-bold text-center text-gray-900 mb-12">
            Featured Quotes
          </h3>
          <div className="space-y-6">
            {quotes.map((quote, index) => (
              <Card
                key={quote.id}
                className="hover:shadow-lg transition-shadow duration-300 animate-fade-in border-l-4 border-l-blue-500"
              >
                <CardContent className="p-6">
                  <div className="flex items-start justify-between">
                    <div className="flex-1">
                      <blockquote className="text-lg text-gray-800 mb-4 italic">
                        "{quote.text}"
                      </blockquote>
                      <div className="flex items-center justify-between">
                        <div>
                          <p className="font-semibold text-gray-900">
                            — {quote.author}
                          </p>
                          <Badge variant="secondary" className="mt-1">
                            {quote.category}
                          </Badge>
                        </div>
                      </div>
                    </div>
                    <div className="flex flex-col items-center space-y-2 ml-6">
                      <Button
                        variant="outline"
                        size="sm"
                        className="hover:bg-green-50 hover:border-green-300"
                        onClick={() => handleVote(quote.id, "up")}
                      >
                        <ChevronUp className="h-4 w-4 text-green-600" />
                        <span className="ml-1 font-semibold">
                          {quote.votes.up}
                        </span>
                      </Button>
                      <Button
                        variant="outline"
                        size="sm"
                        className="hover:bg-red-50 hover:border-red-300"
                        onClick={() => handleVote(quote.id, "down")}
                      >
                        <ChevronDown className="h-4 w-4 text-red-600" />
                        <span className="ml-1 font-semibold">
                          {quote.votes.down}
                        </span>
                      </Button>
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </section>

      {/* Footer */}
      <footer className="bg-gray-900 text-white py-12">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <div className="flex items-center justify-center space-x-3 mb-4">
              <div className="w-8 h-8 bg-blue-600 rounded-lg flex items-center justify-center">
                <Quote className="h-5 w-5 text-white" />
              </div>
              <h3 className="text-2xl font-bold">VoteQuotes</h3>
            </div>
            <p className="text-gray-400 mb-4">
              Empowering democracy through the power of words
            </p>
            <p className="text-sm text-gray-500">
              © 2024 VoteQuotes. All rights reserved.
            </p>
          </div>
        </div>
      </footer>
    </div>
  );
};

export default Index;
